<template>
  <v-navigation-drawer
    app
    temporary
    v-model="$store.state.navigationDrawer"
  >
    <v-toolbar flat>
      <v-list class="pa-0">
        <v-list-tile v-if="checkAuthentication() && user">
          <v-list-tile-avatar><img src="https://randomuser.me/api/portraits/lego/5.jpg" ></v-list-tile-avatar>
          <v-list-tile-content>
            <v-list-tile-title>
              <v-layout row>
                {{ user.username }}
                <v-spacer></v-spacer>
                <v-btn
                  small
                  icon
                  class="mt-0"
                  @click="submitSignout()"
                >
                  <v-icon color="pink">exit_to_app</v-icon>
                </v-btn>
              </v-layout>
            </v-list-tile-title>
          </v-list-tile-content>
        </v-list-tile>
        <v-list-tile v-else :to="{ name: 'Authentication' }">
          <v-list-tile-action><v-icon>lock_open</v-icon></v-list-tile-action>
          <v-list-tile-title>Войти</v-list-tile-title>
        </v-list-tile>
      </v-list>
    </v-toolbar>
    <v-divider></v-divider>
    <v-list>
      <v-list-tile :to="{ name: 'Home' }">
        <v-list-tile-action><v-icon>dashboard</v-icon></v-list-tile-action>
        <v-list-tile-title>Начальная страница</v-list-tile-title>
      </v-list-tile>
      <v-list-tile :to="{ name: 'TestEditor' }">
        <v-list-tile-action><v-icon>library_books</v-icon></v-list-tile-action>
        <v-list-tile-title>Редактор тестов</v-list-tile-title>
      </v-list-tile>
      <v-list-tile :to="{ name: 'QuestionEditor' }">
        <v-list-tile-action><v-icon>question_answer</v-icon></v-list-tile-action>
        <v-list-tile-title>Редактор вопросов</v-list-tile-title>
      </v-list-tile>
    </v-list>
  </v-navigation-drawer>
</template>

<script>
import Authentication from '@/components/pages/Authentication'
import { mapState } from 'vuex'

export default {
  computed: {
    ...mapState([ 'user' ])
  },

  mounted () {
    if (this.checkAuthentication() && !this.user) this.$store.dispatch('getLoggedUser')
  },

  methods: {
    submitSignout () { Authentication.signout(this, '/') },

    checkAuthentication () { return Authentication.checkAuthentication() }
  }
}
</script>



